'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { leaveAPI, attendanceAPI, payrollAPI, notificationAPI } from '@/lib/api';
import { Users, Calendar, DollarSign, Bell } from 'lucide-react';

export default function DashboardPage() {
  const { user } = useAuth();
  const [stats, setStats] = useState({
    pendingLeaves: 0,
    todayAttendance: 0,
    currentMonthPayroll: 0,
    unreadNotifications: 0,
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchDashboardStats();
  }, []);

  const fetchDashboardStats = async () => {
    try {
      const [leaves, attendance, notifications] = await Promise.all([
        leaveAPI.getAll(),
        attendanceAPI.getRecords(),
        notificationAPI.getAll({ isRead: false }),
      ]);

      const pendingLeaves = leaves.data.data?.filter(
        (l: any) => l.status === 'pending'
      ).length || 0;

      const today = new Date().toISOString().split('T')[0];
      const todayAttendance = attendance.data.data?.filter(
        (a: any) => a.date.startsWith(today)
      ).length || 0;

      setStats({
        pendingLeaves,
        todayAttendance,
        currentMonthPayroll: 0,
        unreadNotifications: notifications.data.unreadCount || 0,
      });
    } catch (error) {
      console.error('Error fetching dashboard stats:', error);
    } finally {
      setLoading(false);
    }
  };

  const statCards = [
    {
      title: 'Pending Leave Requests',
      value: stats.pendingLeaves,
      icon: Calendar,
      color: 'text-blue-600',
      bgColor: 'bg-blue-50',
    },
    {
      title: "Today's Attendance",
      value: stats.todayAttendance,
      icon: Users,
      color: 'text-green-600',
      bgColor: 'bg-green-50',
    },
    {
      title: 'Payroll This Month',
      value: `$${stats.currentMonthPayroll}`,
      icon: DollarSign,
      color: 'text-purple-600',
      bgColor: 'bg-purple-50',
    },
    {
      title: 'Unread Notifications',
      value: stats.unreadNotifications,
      icon: Bell,
      color: 'text-orange-600',
      bgColor: 'bg-orange-50',
    },
  ];

  if (loading) {
    return <div className="flex items-center justify-center h-96">Loading...</div>;
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold">Welcome back, {user?.name}!</h1>
        <p className="text-muted-foreground">
          Here's what's happening in your organization today.
        </p>
      </div>

      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        {statCards.map((card) => {
          const Icon = card.icon;
          return (
            <Card key={card.title}>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  {card.title}
                </CardTitle>
                <div className={`p-2 rounded-lg ${card.bgColor}`}>
                  <Icon className={`h-4 w-4 ${card.color}`} />
                </div>
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{card.value}</div>
              </CardContent>
            </Card>
          );
        })}
      </div>

      {/* Quick Actions based on role */}
      <Card>
        <CardHeader>
          <CardTitle>Quick Actions</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
            {user?.role === 'employee' && (
              <>
                <QuickActionButton href="/dashboard/leave" label="Request Leave" />
                <QuickActionButton href="/dashboard/attendance" label="Mark Attendance" />
              </>
            )}
            {(user?.role === 'manager' || user?.role === 'hr_staff' || user?.role === 'admin') && (
              <>
                <QuickActionButton href="/dashboard/leave" label="Review Leaves" />
                <QuickActionButton href="/dashboard/employees" label="Manage Employees" />
                <QuickActionButton href="/dashboard/payroll" label="Process Payroll" />
                <QuickActionButton href="/dashboard/attendance" label="View Attendance" />
              </>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}

function QuickActionButton({ href, label }: { href: string; label: string }) {
  return (
    <a
      href={href}
      className="flex items-center justify-center p-4 border rounded-lg hover:bg-accent transition-colors"
    >
      {label}
    </a>
  );
}
